  grid-column: 2 / 3;
  grid-row: 2 / 3;

  display: flex;
  flex-direction: column;
  gap: 10px;

  padding: 20px;
}

.taskCard {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;

  padding: 10px 20px;

  background-color: white;
  border-radius: 15px;
}

.leftElement,
.rightElement {
  display: flex;
  gap: 10px;
}

.btnRemoveTaskContainer img {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  width: 20px;
  height: 20px;

  border-radius: 100%;
}

.addTasksContainer {
  grid-column: 4 / 5 ;
  grid-row: 2 / 3;

  display: flex;
  align-items: flex-end;
  justify-content: center;

  padding: 50px 20px;
}

.btnAddTasksContainer {
  background-color: var(--default-color);
  border-radius: 100rem;

  width: 60px;
  height: 60px;
}

.addTasksContainer img {
  filter: invert(100%);
}

.newTaskForm {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
}`, "",{"version":3,"sources":["webpack://./src/styles/tasks.css"],"names":[],"mappings":"AAAA;EACE,kBAAkB;EAClB,eAAe;;EAEf,aAAa;EACb,sBAAsB;EACtB,SAAS;;EAET,aAAa;AACf;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,SAAS;;EAET,kBAAkB;;EAElB,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;;EAEE,aAAa;EACb,SAAS;AACX;;AAEA;EACE,yDAAoD;EACpD,sBAAsB;EACtB,WAAW;EACX,YAAY;;EAEZ,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;EACnB,eAAe;;EAEf,aAAa;EACb,qBAAqB;EACrB,uBAAuB;;EAEvB,kBAAkB;AACpB;;AAEA;EACE,sCAAsC;EACtC,qBAAqB;;EAErB,WAAW;EACX,YAAY;AACd;;AAEA;EACE,oBAAoB;AACtB;;AAEA;EACE,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,gCAAgC;;EAEhC,aAAa;EACb,sBAAsB;AACxB","sourcesContent":[".tasksContainer {\n  grid-column: 2 / 3;\n  grid-row: 2 / 3;\n\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n\n  padding: 20px;\n}\n\n.taskCard {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  gap: 20px;\n\n  padding: 10px 20px;\n\n  background-color: white;\n  border-radius: 15px;\n}\n\n.leftElement,\n.rightElement {\n  display: flex;\n  gap: 10px;\n}\n\n.btnRemoveTaskContainer img {\n  background-image: url('../assets/images/remove.png');\n  background-size: cover;\n  width: 20px;\n  height: 20px;\n\n  border-radius: 100%;\n}\n\n.addTasksContainer {\n  grid-column: 4 / 5 ;\n  grid-row: 2 / 3;\n\n  display: flex;\n  align-items: flex-end;\n  justify-content: center;\n\n  padding: 50px 20px;\n}\n\n.btnAddTasksContainer {\n  background-color: var(--default-color);\n  border-radius: 100rem;\n\n  width: 60px;\n  height: 60px;\n}\n\n.addTasksContainer img {\n  filter: invert(100%);\n}\n\n.newTaskForm {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n\n  display: flex;\n  flex-direction: column;\n}"],"sourceRoot":""}]);
