import { saveDataToStorage, getDataFromStorage, getAllKeysFromStorage } from './storage.js';
import taskEdit from './taskEdit.js';
import { setCurrentPage, getCurrentPage } from './globalVariables.js';

//Create Tasks container
const tasksContainer = document.createElement('div');
tasksContainer.classList.add('tasksContainer');

const removeTask = (tasksArray, taskId) => {
  // Find the index of the task in the tasksArray that matches the given taskId
  const index = tasksArray.findIndex((task) => task.id === taskId);

  // If the task with the given taskId is found in the tasksArray
  if (index !== -1) {
    // Use the splice() method to remove one element at the found index from the tasksArray
    tasksArray.splice(index, 1);

    // Update the local storage with the updated tasksArray after removal
    saveDataToStorage('tasksArray', tasksArray);
  }
}

//Filter date function
const filterDate = (tasks, startDate, endDate) => {
  return tasks.filter((task) => {
    const taskDate = new Date(task.date);
    taskDate.setHours(0, 0, 0, 0);
    startDate.setHours(0, 0, 0, 0);
    endDate.setHours(0, 0, 0, 0);
    return taskDate <= endDate && taskDate >= startDate;
  });
}

//Sort tasks by earliest date to latest date
const sortTasksByDate = (tasks) => {
  return tasks.sort((a, b) => new Date(a.date) - new Date(b.date) );
}

//Get refernce for the current page
let currentPage = getCurrentPage();

//Date ranges
const today = new Date();
const dateRanges = {
  today: {
    startDate: new Date(),
    endDate: new Date()
  },
  thisWeek: {
    startDate: new Date(today.getFullYear(), today.getMonth(), today.getDate() - today.getDay()),
    endDate: new Date(today.getFullYear(), today.getMonth(), today.getDate() + (6 - today.getDay()))
  },
  thisMonth: {
    startDate: new Date(today.getFullYear(), today.getMonth(), 1),
    endDate: new Date(today.getFullYear(), today.getMonth() + 1, 0)
  },
  all: {
    startDate: new Date(1900, 0, 1), 
    endDate: new Date(2100, 11, 31) 
  }
};

const tasks = () => {
  const content = document.querySelector('.content');

  //Create Tasks Array from the values in local storage
  let tasksArray = getDataFromStorage('tasksArray') || [];
  saveDataToStorage('tasksArray', tasksArray);

  const renderTasks = (selectedDateRange) => {

    tasksArray = getDataFromStorage('tasksArray') || [];

    tasksContainer.textContent = '';

    let filteredArray = tasksArray;
    const { startDate, endDate } = dateRanges[selectedDateRange];
    
    //If 'all' tab is elected, display all tasks regardless if task has a date value or not
    if (selectedDateRange === 'all') {
      filteredArray = tasksArray
    }
    else {
      let filteredDates = filterDate(tasksArray, startDate, endDate);
      filteredArray = sortTasksByDate(filteredDates);
    }

    for (let i = 0; i < filteredArray.length; i++) {
      //Create task container
      const taskCard = document.createElement('div');
      taskCard.classList.add('taskCard');

      //Set unique ID
      filteredArray[i].id = i;

      //Create Task title
      const taskTitle = document.createElement('h4');
      taskTitle.textContent = filteredArray[i].title;

      //Create date info
      const taskDate = document.createElement('p');
      taskDate.classList.add('taskDate');
      taskDate.textContent = filteredArray[i].date;

      //Create checkbox
      const checkBox = document.createElement('input');
      checkBox.type = 'checkbox';
      checkBox.id = 'taskCheckBox';

      checkBox.checked = filteredArray[i].isChecked || false;

      //Add 'change' event on the checkbox
      checkBox.addEventListener('change', (event) => {
        filteredArray[i].isChecked = event.target.checked;
        if (filteredArray[i].isChecked === true) {
          console.log(filteredArray[i].isChecked);
        }
        else {
          console.log(filteredArray[i].isChecked)
          console.log(filteredArray[i].id)
        }
        saveDataToStorage('tasksArray', tasksArray);
      });

      //Add click event to prevent stop propogation to the taskCard.
      //So that when clicking on the checkbox, the click does not trigger
      //the taskCard events also.
      checkBox.addEventListener('click', (event) => {
        event.stopPropagation();
      });

      //Add remove button
      const btnRemoveTaskContainer = document.createElement('div');
      btnRemoveTaskContainer.classList.add('btnRemoveTaskContainer');
      const btnRemoveTask = document.createElement('img');
      btnRemoveTaskContainer.appendChild(btnRemoveTask);

      btnRemoveTask.addEventListener('click', (event) => {
        event.stopPropagation();
        removeTask(tasksArray, tasksArray[i].id);
        renderTasks(selectedDateRange);
      });

      //Run task edit when task card is clicked on
      taskCard.addEventListener('click', () => {
        taskEdit();
      })

      saveDataToStorage('tasksArray', tasksArray);

      //CSS styling containers
      const rightElement = document.createElement('div');
      rightElement.classList.add('rightElement');
      const leftElement = document.createElement('div');
      leftElement.classList.add('leftElement');

      leftElement.appendChild(checkBox);
      leftElement.appendChild(taskTitle)
      rightElement.appendChild(taskDate);
      rightElement.appendChild(btnRemoveTaskContainer);
      taskCard.appendChild(leftElement);
      taskCard.appendChild(rightElement);
      tasksContainer.appendChild(taskCard);
    }
  }

  content.appendChild(tasksContainer);

  return { 
    tasksArray,
    tasksContainer,
    renderTasks
  }
}

export default tasks;